define('config/routes/tiles/tile', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var TilesTileRoute;

  TilesTileRoute = Ember['default'].Route.extend({
    model: function model(arg) {
      var tileId;
      tileId = arg.tileId;
      return this.store.find("tile", tileId);
    }
  });

  exports['default'] = TilesTileRoute;

});